<template>
  <div>
    <mu-card>
      <!--<mu-card-header :title="nickname" :subTitle="time">
        <mu-avatar :src="avatar" slot="avatar"/>
      </mu-card-header>-->
      <mu-card-media>
        <img :src="img" />
      </mu-card-media>
      <mu-card-title :title="title"/>
      <mu-card-text>
        <div class="signature">
          <span>{{nickname}}</span><span class="time"> | {{time}}</span>
        </div>
      </mu-card-text>
    </mu-card>
  </div>
</template>
<style>
  .mu-card{
    border-radius: 5px;
    overflow: hidden;
    margin-top: 15px;
  }
  .mu-card-media>img{
    border-radius: 5px 5px 0 0;
  }
  .mu-card-title-container{
    padding: 8px 12px 0;
  }
  .mu-card-text{
    padding: 0 12px 12px;
    font-size: 14px;
    color: #999;
  }
  .mu-card-text>.signature{
    padding-top: 15px;
    font-size: 12px;
    color: #333;
  }
  .mu-card-text>.signature .time{
    font-size: 12px;
    color: #999;
  }
  .mu-card-title-container .mu-card-title{
    font-size: 19px;
    line-height: 21px;
    color: #333;
    overflow: hidden;
    text-overflow:ellipsis;
	  white-space:nowrap;
  }
</style>

<script>
import avatar from '../assets/coder.png'
import defaultPic from '../assets/153.jpg'
import githubImg from '../assets/github.png'
import javascriptImg from '../assets/javascript.png'
import mysqlImg from '../assets/mysql.png'
import nodeImg from '../assets/node.png'
import othersImg from '../assets/others.png'
import vueImg from '../assets/vue.png'
import reactImg from '../assets/react.png'
export default {
  name: 'card-item',
  data () {
    return {
      avatar
    }
  },
  props: {
    nickname: {
      type: String,
      default: 'liujians'
    },
    type: {
      type: String
    },
    time: {
      required: true
    },
    imgSrc: {
      default: defaultPic
    },
    title: {
      type: String,
      required: true
    },
    content: {
      type: String,
      required: true
    }
  },
  computed: {
    img () {
      switch (this.$route.params.type) {
        case 'demo' :
          return githubImg
        default :
          switch (this.$props.type) {
            case 'Vue' :
              return vueImg
            case 'MySql' :
              return mysqlImg
            case 'Javascript' :
              return javascriptImg
            case 'Node' :
              return nodeImg
            case 'Others' :
              return othersImg
            case 'React' :
              return reactImg
          }
      }
    }
  }
}
</script>



